@import '../../constants/variable';

.c_data
{
    background-color: $bg-right;
    box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.16);
    font-size: 13px;
    height: 100%;
    
    .title
    {
        background-color: $bg-right-title;
        color: $font-title;
        height: 20px;
        line-height: 20px;
        padding: 10px $div-gap;
        display: flex;
        justify-content: space-between;

        img
        {
            cursor: pointer;
        }
    }

    .info
    {
        display: flex;
        padding: 20px $div-gap;
        color: $font-gray;

        @include rwd(phone)
        {
           flex-direction: column;
        }

        > div
        {
            margin-right: 10%;
            flex: 1;

            &:last-child
            {
                margin-right: 0;
            }
        }

        .name
        {
            border-bottom: dotted 1px #4a4a4a;
            padding-bottom: $div-gap;
            
            h2
            {
                font-size: 14px;
                margin: 0;
                margin-bottom: 4px;
            }
        }
        
        .intro
        {
            h3
            {
                font-size: 12px;
            }

            p
            {
                color: $font-title;
                font-size: 14px;
                line-height: 1.62;
            }
        }

        .detail
        {
            display: flex;
            flex-direction: column;

            @include rwd(phone)
            {
               margin-top: 30px;
            }

            > div
            {
                display: flex;
                margin-bottom: 20px;

                > div
                {
                    &:first-child
                    {
                        width: 120px;
                    }
                    
                    &:last-child
                    {
                        color: $font-title;
                        margin-left: 24px;
                        word-break: break-all;
                        width: calc(100% - 144px);
                        line-height: 1.38;
                    }
                }
            }

            a
            {
                color: $font-title;
                text-decoration: none;
            }
        }
    }
}