@import '../../constants/variable';

.co_currency
{
    background-color: $bg-black;
    position: relative;
    height: 100%;
    min-height: 100vh;

    @include rwd(phone)
    {
        height: 100%;
        min-height: 100vh;
    }

    nav
    {
        @include nav;
    }

    main
    {
        padding: 20px 40px;
        display: flex;
        // height: calc(100vh - 186px);

        @include rwd(phone)
        {
           padding: 0;
           height: auto;
           padding-bottom: 180px;
        }
    
        .title
        {
            @include title($bg-right-title);
        }

        .sidebar
        {
            width: 30%;
            background-color: $bg-right;
            margin-right: $div-gap;
            position: relative;
            display: flex;
            flex-direction: column;

            @include rwd(phone)
            {
               display: none;
            }

            ul
            {
                list-style: none;
                color: #fff;
                font-size: 12px;
                margin: 0;
                padding: 0;
                overflow: auto;
                flex: 1;

                li
                {
                    padding: 12px $div-gap;
                    cursor: pointer;
                    position: relative;

                    span
                    {
                        margin-left: 8px;
                    }

                    &::after
                    {
                        content: '';
                        position: absolute;
                        bottom: 0;
                        left: 5%;
                        width: 90%;
                        border-bottom: 1px dashed #444;
                    }

                    &.active
                    {
                        background-color: #000;
                    }

                    &:hover
                    {
                        background-color: rgba(0, 0, 0, .7);
                    }
                }
            }
        }

        .content
        {
            flex: 1;

            @include rwd(phone)
            {
                width: 96%;
                margin: 0 auto;
            }
        }
    }
}