@import '../../constants/variable';

footer
{
    background-color: $bg-right;
    color: $font-gray;
    display: flex;
    font-size: 12px;
    position: absolute;
    bottom: 0;
    width: 100%;
    flex-direction: column;
    

    p
    {
        margin-right: 32px;
        margin-left: 40px;
        display: flex;
        align-items: center;
        a
        {
            text-decoration: none;
            color: $font-gray;

            &:hover
            {
                color: #fff;
            }
        }

        span
        {
            font-size: 16px;
            margin: 4px 5px;
            margin-bottom: 0;
            font-weight: lighter;
        }
    }

    > div
    {
        margin-left: 40px;
    }
}
